<template>
  <div>
    <v-row justify="center">
      <v-col v-if="statusSuccess" cols="12" class="text-center">
        {{ yourTicketHasBeenSent }}
        <v-icon class="theme-color" x-large>mdi-checkbox-marked-circle-outline</v-icon>
      </v-col>
      <v-col v-if="!statusSuccess" cols="12" class="text-center">
        {{ ticketWasNotSentError }}
        <v-icon class="theme-color" x-large>mdi-close-circle-outline</v-icon>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { statusTypes } from '@/utils/types'
import { mapState } from 'vuex'

export default {
  name: 'Status-header',
  props: {
    status: { type: String, required: true }
  },
  computed: {
    ...mapState({
      config: ({ appState }) => appState.config,
      textKeys: ({ appState }) => appState.textKeys
    }),
    statusSuccess () {
      return this.status === statusTypes.SUCCESS
    },
    yourTicketHasBeenSent () {
      return this.textKeys.yourTicketHasBeenSent || '{yourTicketHasBeenSent}'
    },
    ticketWasNotSentError () {
      return this.textKeys.ticketWasNotSentError || '{ticketWasNotSentError}'
    }
  }
}
</script>

